#invoice-POS{
  padding: 20px 30px;
  background: #FFF;
  font-family:'Courier New', Courier, monospace;
  font-weight:bolder;
}
 
h1{
  font-size: 1.5em;
  color: #222;
}
h2{font-size: .9em;}
h3{
  font-size: 1.2em;
  font-weight: 500;
  line-height: 2em;
}
p{
  font-size: .90em;
  color: rgb(37, 32, 32);
  line-height: 1.5em;
}
 

#top{min-height: 100px;}
#mid{min-height: 80px;} 
#bot{ min-height: 50px;}

.info{
  display: block;
}

.info h3{
  font-weight:bolder;
  font-size: 20px;
  padding:5px 10px;
  color:rgb(32, 22, 22);
  border: 4px solid #ccc;
  text-align:center;
}

.title{
  float: right;
}
.title p{text-align: right;} 

table{
  width: 100%;
  border-collapse: collapse;
  border: thin solid #ddd;
}

#invoice-POS td:first-child, #invoice-POS td:last-child, #invoice-POS td:nth-child(2){
  text-align:left;
}

#invoice-POS td:first-child {
  text-indent:5px;
  padding-left: 5px !important;
}

.tabletitle{
  padding-top:20px !important;
  font-weight:bolder;
  background: #eee;
  text-align:center;
  padding-bottom:10px;
  text-transform:uppercase !important;
  font-size: 14px;
  color: rgb(78, 72, 72);
  line-height: 1.5em;
}
.service{
  text-align:center;
  border-bottom:thin solid #ddd;

}
.item{width: 24mm;}

.tabletitle h2{
  text-transform:uppercase !important;
  font-weight:bolder;
  font-size: 14px;
  color: rgb(78, 72, 72);
  line-height: 1.5em;
 
}
.itemtext{
  font-size: 0.9em;
  font-weight:bolder;
  color: rgb(66, 62, 62);
  font-style:normal;
  line-height: 1.5em;
}

#legalcopy{
  margin-top: 5mm;
}

@media print {
  .printbox {
    page-break-after:always !important;
  }
}

